.letsKeepIn {
  position: relative;
  font-size: var(--h3-size);
  line-height: 50px;
  color: var(--neutral-1);
  text-align: center;
}
.nameSection {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.message,
.nameSection {
  align-self: stretch;
}
.message,
.text {
  position: relative;
  line-height: 30px;
}
.eyeRegularIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.search {
  align-self: stretch;
  flex: 1;
  background-color: var(--neutral-light-3);
  border: 1px solid var(--color-lightsteelblue);
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.text1 {
  position: relative;
  line-height: 22px;
  font-weight: 600;
}
.arrowrightRegularIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.wireframeButton {
  background-color: var(--gray-500);
  height: 46px;
  display: none;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-7xs);
  font-size: var(--text-m-semi-bold-size);
  color: var(--neutral-100);
}
.box,
.hintText {
  align-self: stretch;
}
.box {
  flex: 1;
  border-radius: var(--br-5xs);
  border: 1px solid var(--neutral-2);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  color: var(--color-lightsteelblue);
}
.hintText {
  position: relative;
  font-size: var(--text-s-regular-size);
  letter-spacing: -0.1px;
  line-height: 20px;
  color: var(--gray-50);
  display: none;
}
.buttons,
.form,
.wireframeInput {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.wireframeInput {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  gap: var(--gap-5xs);
}
.buttons,
.form {
  flex-direction: row;
}
.form {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--neutral-light-3);
  width: 808px;
  height: 694px;
  flex-direction: column;
  padding: var(--padding-13xl);
  box-sizing: border-box;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--body-midd-size);
  color: var(--neutral-2);
  font-family: var(--text-s-regular);
}
