@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --text-s-regular: Inter;

  /* font sizes */
  --text-s-regular-size: 14px;
  --text-m-semi-bold-size: 15px;
  --h4-size: 24px;
  --body-midd-size: 16px;
  --h3-size: 38px;
  --h1-size: 64px;
  --font-size-xl: 20px;

  /* Colors */
  --gray-50: #5f6d7e;
  --gray-500: #2e3646;
  --neutral-100: #f8f9fb;
  --primary-1: #6a35f5;
  --color-black: #000;
  --neutral-light-4: #fff;
  --color-gray-100: #131126;
  --neutral-1: #07090a;
  --neutral-light-3: #f7f7fc;
  --neutral-2: #4e4775;
  --color-lightsteelblue: #a0a3bd;
  --primary-4: #dccfff;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-7xs: 6px;
  --gap-base: 16px;
  --gap-541xl: 560px;
  --gap-96xl: 115px;
  --gap-37xl: 56px;
  --gap-5xl: 24px;
  --gap-6xs: 7px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-lg: 18px;
  --padding-base: 16px;
  --padding-21xl: 40px;
  --padding-5xl: 24px;
  --padding-45xl: 64px;
  --padding-13xl: 32px;

  /* Border radiuses */
  --br-5xs: 8px;
  --br-3xs: 10px;
  --br-11xs-4: 1.4px;
  --br-mid: 17px;
  --br-81xl: 100px;
}
