.label,
.text {
  position: relative;
}
.label {
  align-self: stretch;
  line-height: 20px;
  font-weight: 500;
}
.text {
  flex: 1;
  line-height: 22px;
  font-weight: 600;
}
.eyeRegularIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.search {
  flex: 1;
  background-color: var(--neutral-100);
  border: 1px solid var(--gray-50);
  box-sizing: border-box;
  height: 46px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.placeholderRegularIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.text1 {
  position: relative;
  line-height: 22px;
  font-weight: 600;
}
.wireframeButton {
  background-color: var(--gray-500);
  height: 46px;
  display: none;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-7xs);
  color: var(--neutral-100);
}
.box,
.hintText {
  align-self: stretch;
}
.box {
  border-radius: var(--br-5xs);
  border: 1px solid var(--gray-50);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--text-m-semi-bold-size);
}
.hintText {
  position: relative;
  letter-spacing: -0.1px;
  line-height: 20px;
  display: none;
}
.typedefaultSizemedium {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  text-align: left;
  font-size: var(--text-s-regular-size);
  color: var(--gray-50);
  font-family: var(--text-s-regular);
  flex: 1;
}
