.firstName,
.text {
  position: relative;
  line-height: 30px;
}
.firstName {
  align-self: stretch;
}
.text {
  flex: 1;
}
.eyeRegularIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.search {
  flex: 1;
  background-color: var(--neutral-light-3);
  border: 1px solid var(--color-lightsteelblue);
  box-sizing: border-box;
  height: 46px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.text1 {
  position: relative;
  line-height: 22px;
  font-weight: 600;
}
.arrowrightRegularIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.wireframeButton {
  background-color: var(--gray-500);
  height: 46px;
  display: none;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-7xs);
  font-size: var(--text-m-semi-bold-size);
  color: var(--neutral-100);
}
.box,
.wireframeInput {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.box {
  align-self: stretch;
  border-radius: var(--br-5xs);
  border: 1px solid var(--neutral-2);
  overflow: hidden;
  flex-direction: row;
  color: var(--color-lightsteelblue);
}
.wireframeInput {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-5xs);
  text-align: left;
  font-size: var(--body-midd-size);
  color: var(--neutral-2);
  font-family: var(--text-s-regular);
}
